nce to her burrow while she is inside: on emerging, she will repeat the whole procedure as often as the food is displaced. Intelligence—conspicuously absent in the case of Sphex—must include the ability to adapt to new circumstances.")]),_c('p',[_vm._v("Psychologists generally do not characterize human intelligence by just one trait but by the combination of many diverse abilities. Research in AI has focused chiefly on the following components of intelligence: learning, reasoning, problem solving, perception, and using language.")])]):_vm._e(),(_vm.content.type == 'design')?_c('div',[_c('p',[_vm._v("Graphic design is a craft where professionals create visual content to communicate messages. By applying visual hierarchy and page layout techniques, designers use typography and pictures to meet users’ specific needs and focus on the logic of displaying elements in interactive designs, to optimize the user experience.")]),_c('p',[_vm._v("Graphic design is an ancient craft, dating back past Egyptian hieroglyphs to at least 17,000-year-old cave paintings. It’s a term that originated in the 1920s’ print industry. It continues to cover a range of activities including logo creation. Graphic design in this sense concerns aesthetic appeal and marketing. Graphic designers attract viewers using images, color and typography. However, graphic designers working in")]),_c('p',[_vm._v("user experience (UX) design must justify stylistic choices regarding, say, image locations and font with a human-centered approach. That means you need to focus on—and seek to empathize the most with—your specific users while you create good-looking designs that maximize usability. Aesthetics must serve a purpose—in UX design we don’t create art for art’s sake. So, graphic designers must branch into visual design. When designing for UX, you should:")])]):_vm._e(),(_vm.content.type == 'mobile')?_c('div',[_c('p',[_vm._v("Mobile development is doing any kind of development for any kind of mobile device. Somewhat of a rhetorical definition, but stay with me here. What I mean by this statement is that mobile development encompases developing apps for phones, tablets, smart watches, and all other kinds of wearable devices which run some kind of mobile operating system. It also doesn’t necessarily mean developing purely mobile applications, since even web developers today have to think about how their applications are going to be used and accessed on a mobile device.")]),_c('p',[_vm._v("It also doesn’t necessarily mean developing purely mobile applications, since even web developers today have to think about how their applications are going to be used and accessed on a mobile device.")]),_c('p',[_vm._v("In fact, as we’ll discuss a little later in this chapter, mobile applications can even be developed exclusively for mobile devices but entirely as web applications. This may even be the trend of the future as mobile devices become more and more powerful, and the browser takes an even more dominant role as the operating system of the future.")])]):_vm._e(),(_vm.content.type == 'web')?_c('div',[_c('p',[_vm._v("Whether you’re just starting a business or want to move your existing brick and mortar business online, you’ll need to locate a website builder to get you and your brand online.")]),_c('p',[_vm._v("Website builders come with a variety of options, so it is important to find one that fits you and your website.")]),_c('p',[_vm._v("A website builder is software that allows you to create a website online. The software will reside on a web server at a hosting company or be part of a hosted SaaS (software as a service) platform.")]),_c('p',[_vm._v("Or in other words, you do not use your local computer (desktop or laptop) to hold software that will build the website. Instead you’ll build the website online via software designed specifically for website creation.")]),_c('p',[_vm._v("Web development is growing and expanding its periphery. From automating web interfaces to developing mission-critical applications, web development is turning dreams into realities. The section includes articles, guides and case studies about web development, application development and website designing. Read on to discover expert articles and opinions about different aspects of website development and designing.")])]):_vm._e(),_c('button',{staticClass:"m-button-primary btn mt-5"},[_vm._v("Order Now")])])])],1)],1)],1)}
